.Categories {
  background: var(--lightGray);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.Categories .customButton {
  color: var(--white);
  height: 72px;
  width: 176px;
  cursor: pointer;
  margin-bottom: 5rem;
}
.Categories .categoriesContent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-block: 80px;
  gap: 30px;
}
