.cart {
  display: flex;
  /* align-items: center; */
  justify-content: center;
  flex-direction: column;
  background-color: #f8f8f8 !important;
}

.cart .cartBreadcrumb {
  color: var(--black);
  font-family: Sora;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  /* 125% */
  letter-spacing: 0.1px;
}

.cart .cardBorder {
  border: 1px solid #0000001a;
  border-radius: 20px;
  margin: 1rem 0;
}

.cart .empty_cart {
  width: 100%;
  height: 215px;
  display: flex;
  justify-content: center;
  align-items: center;
}
